    import { mapState } from 'vuex'
    export default {
        data() {
            return {
                info:{},
	            pdfShow:false,
	            videoShow:false,
	            isOne:false,
	            isTwo:false,
	            pdfFlag:false,
	            videoFlag:false,
	            btnFlag:false,
				content:''
            };
        },
        created:function () {
            this.$store.dispatch('showLoading')
			this.info = this.$route.query.info
	        if(this.info.pdf && this.info.video){
	        	this.btnFlag = true;
	        	this.pdfFlag = true;
	        	this.videoFlag = true;
	        	this.isTwo = true;
	        }else if(this.info.pdf){
		        this.btnFlag = true;
		        this.pdfFlag = true;
		        this.isOne = true;
	        }else if(this.info.video){
		        this.btnFlag = true;
		        this.videoFlag = true;
		        this.isOne = true;
	        }

            var ssrBase = ''
			let _this = this;
            $.get(ssrBase+this.info.article,function (res) {
				_this.content = res;
                _this.$store.dispatch('hideLoading')
            });

			$(function(){
                $(".navbar-center").css('marginLeft',0);
            });
        },
        methods: {
            toPDF:function () {
	            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
		            openFile(this.info.pdf)
	            }else{
		            openPdf(this.info.pdf)
	            }
            },
            toVideo: function () {
	            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
		            openFile(this.info.video)
	            }else{
		            openVideo(this.info.video)
	            }
            }
        },
        mounted:function () {
            //falg
            isHomePage(0)
        },
        components:{
        }
    };



