	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				panoramicSrc:'',
				tabIndex:''
			};
		},
		created:function () {
            $(function(){
                $(".navbar-center").css('marginLeft',0);
            });
		},
        mounted:function () {
            //falg
            isHomePage(0)
        },
		methods:{
            goDetail: function (url,flag) {
                //
                let _this = this
                var lifeFlag = JSON.parse(localStorage.getItem('lifeFlag'));
                console.log(lifeFlag)
                console.log('g',$.inArray(flag, lifeFlag))
                if(lifeFlag){
                    if($.inArray(flag, lifeFlag) > -1){
                        if(/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
                            console.log(url)
                            openFile(url)
                        } else {
                            openUrl(url,' ')
                        }
                    } else {
                        var newLife = JSON.parse(localStorage.getItem('lifeFlag'))
                        newLife.push(flag)
                        localStorage.setItem('lifeFlag',JSON.stringify(newLife));
                        _this.$dialog.confirm({
                            title: ' ',
                            mes: _this.language.msg.lifeMsg,
                            opts: () => {
                                console.log(url);
                                if(/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
                                    console.log(url)
                                    openFile(url)
                                } else {
                                    // console.log(url);
                                    openUrl(url,' ')
                                }
                            }
                        });
                        //
                        if(localStorage.LANGUAGE!='zh'){
                            $(".confirm-bd").css('word-break',"keep-all");
                        }else{
                            $(".confirm-bd").css('word-break',"break-all");
                        }
                    }
                } else {
                    var newLife = []
                    newLife.push(flag)
                    localStorage.setItem('lifeFlag',JSON.stringify(newLife));
                    _this.$dialog.confirm({
                        title: '',
                        mes: _this.language.msg.lifeMsg,
                        opts: () => {
                            if(/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
                                console.log(url)
                                openFile(url)
                            } else {
                                openUrl(url,' ')
                            }
                        }
                    });
                }

            }
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })

        },
	};



