// 
axios.interceptors.request.use(function (config) {
	//loading 
	store.dispatch('showLoading');
	return config;
}, function (error) {
    router.push('/error')
    store.dispatch('hideLoading');
	return Promise.reject(error);
});
// 
axios.interceptors.response.use(function (response) {
	// loading 
	store.dispatch('hideLoading');
	return response;
}, function (error) {
	router.push('/error')
    store.dispatch('hideLoading');
	return Promise.reject(error);
});

