
import { mapState } from "vuex";
import { mapGetters } from "vuex";
import foot from "../foot.vue";
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { getDistance } from "../../config/mUtils";


export default {
data() {
return {
isItemShow: false,
typeList: [],
dataList: [],
nextPage: 0,
distanceFlag: false,
typeId: 1,
noData: false,
tabNum:1,
data:{},
srcUrl:'',
pdfShow:false,
videoShow:false,
isOne:false,
isTwo:false,
pdfFlag:false,
videoFlag:false,
btnFlag:false,
content:''
};
},
created: function() {
localStorage.footFlag = 3;

$(function(){
$(".navbar-center").css('marginLeft',0);
});
},
mounted: function() {
let _this = this;

let params = {
hotelid: localStorage.HOTELID,
lang: localStorage.LANGUAGE
};
this.$store.dispatch("getPoiTypeList", params).then(function(res) {
_this.typeList = res.data.data.list;
_this.typeId = _this.typeList[0].id;
$(document).ready(function() {
var mySwiper =new Swiper(".raiders .swiper-container", {
pagination: ".swiper-pagination",
slidesPerView: 3,
paginationClickable: true,
spaceBetween: 0,
initialSlide :0
});

$(".ra").on("click", ".swiper-slide", function() {
$(this)
.addClass("active")
.siblings()
.removeClass("active");
});


if (_this.nextPage != "-1") {
var flag = "inifinte";
var id = _this.typeId;
_this.getList(id, flag);
} else {
done(false);
_this.noData = true;
}


$("body").scrollTop(0);
$('body').css('overflow-y', 'hidden');
});
});


isHomePage(1);
},
methods: {
detailClose:function(){
$("#section1").show();
$("#section2").hide();

$("body").scrollTop(0);
$('body').css('overflow-y', 'hidden');
},
toPDF:function () {
if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
openFile(this.data.pdf)
}else{
openPdf(this.data.pdf)
}
},
toVideo: function () {
if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
openFile(this.data.video)
}else{
openVideo(this.data.video)
}
},
openMap: function () {
openMap(this.data.lng,this.data.lat)
},
detailInit:function(){
this.$store.dispatch('showLoading');
if(this.data.pdf && this.data.video){
this.btnFlag = true
this.pdfFlag = true
this.videoFlag = true
this.isTwo = true
}else if(this.data.pdf){
this.btnFlag = true
this.pdfFlag = true
this.isOne = true
}else if(this.data.video){
this.btnFlag = true
this.videoFlag = true
this.isOne = true
}

var ssrBase = ''
let _this = this;
if (this.data.detail) {
$.get(ssrBase+this.data.detail,function (res) {
_this.content = res;
_this.$store.dispatch('hideLoading')
})
} else {
this.$store.dispatch('hideLoading')
}
},
getList: function(id, flag) {
let _this = this;
_this.typeId = id;
if (flag == "click") {
this.dataList = [];
_this.nextPage = 0;
_this.noData = false;
}
setTimeout(function() {
let params = {
hotelid: localStorage.HOTELID,
typeid: id,
lang: localStorage.LANGUAGE,
limit: 30,
page: _this.nextPage,
status:1
};
_this.$store.dispatch("getPoiList", params).then(res => {
let list = res.data.data.list;
_this.nextPage = res.data.data.nextPage;

var dataArr = [];
for (var item in list) {
let distance = parseFloat(
getDistance(
localStorage.lat,
localStorage.lng,
list[item].lat,
list[item].lng
)
).toFixed(2);
list[item].distance = distance;
dataArr.push(list[item]);
}
_this.dataList = _this.dataList.concat(dataArr);
});
}, 300);
},
infinite:function (done) {
let _this = this
done(true);

if(this.nextPage != '-1'){
var flag = 'inifinte'
var id =this.typeId
this.getList(id, flag)
}else{
done(false);
this.noData = true;
}
},
changeTab: function(id) {
this.tabNum = id;
var flag = "click";
this.getList(id, flag);
},
toDetail: function(id) {
let data = {};
for (var key in this.dataList) {
if (this.dataList[key].id == id) {
data = this.dataList[key];
break;
}
}
$("#section1").hide();
$("#section2").show();

$("body").scrollTop(0);
$('body').css('overflow-y', 'auto');
this.data = data;
this.detailInit();
}
},
components: {
"v-foot": foot,
PopupPicker
},
computed: {
...mapState({
language: state => state.language.language
})
},
destroyed: function () {
$("body").scrollTop(0);
$("body").css('overflow-y', 'auto');
}
};



