import { mapState } from 'vuex'
export default {
    data() {
        return {
            center: [],
            zoom:14,
            markers: [],
            stateFlag:''
        };
    },
    created: function () {
        let _this = this
        this.stateFlag = localStorage.stateFlag
        this.center = [_this.home.data.lng,_this.home.data.lat]
        this.markers = [
            {
                position: [_this.home.data.lng,_this.home.data.lat],
                events: {
                    click: (e) => {
                        openMap(e.lnglat.lng,e.lnglat.lat)
                    },
                    dragend: (e) => {
                        this.markers[0].position = [e.lnglat.lng, e.lnglat.lat];
                    }
                },
                visible: true,
                draggable: false,
                icon: new AMap.Icon({  //
                    size: new AMap.Size(110, 52),//
                    image: require('../../assets/images/icon-guide.png'), //
                    imageOffset: new AMap.Pixel(0, 0)//
                })
            }
        ];

        $(function(){
            $(".navbar-center").css('marginLeft',0);
        });
    },
    methods: {
        audioControl:function () {
            var audio = document.getElementById('voiceControl')
            if(audio.paused){
                audio.play()
            } else {
                audio.pause()
            }
        }
    },
    mounted() {
    },
    computed: {
        ...mapState({
            language: state => state.language.language,
            home: state => state.home.home,
            hotelDetail: state => state.home.hotelDetail
        })
    },
}



