    import { mapState } from 'vuex'
    export default {
        data() {
            return {
                data:{},
				srcUrl:'',
	            pdfShow:false,
	            videoShow:false,
	            isOne:false,
	            isTwo:false,
	            pdfFlag:false,
	            videoFlag:false,
	            btnFlag:false,
				content:'', 
            }
        },
        created:function () {
			this.data = this.$route.query.data 
			console.log('hhhhh',this.data)
            this.$store.dispatch('showLoading')
	        if(this.data.pdf && this.data.video){
		        this.btnFlag = true
		        this.pdfFlag = true
		        this.videoFlag = true
		        this.isTwo = true
	        }else if(this.data.pdf){
		        this.btnFlag = true
		        this.pdfFlag = true
		        this.isOne = true
	        }else if(this.data.video){
		        this.btnFlag = true
		        this.videoFlag = true
		        this.isOne = true
	        }

            var ssrBase = ''
            let _this = this;
	        if (this.data.detail) {
                $.get(ssrBase+this.data.detail,function (res) {
                    _this.content = res;
                    _this.$store.dispatch('hideLoading')
                })
			} else {
                this.$store.dispatch('hideLoading')
			}
			
			$(function(){
                $(".navbar-center").css('marginLeft',0);
            });
        },
        methods: {
	        toPDF:function () {
		        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
			        openFile(this.data.pdf)
		        }else{
			        openPdf(this.data.pdf)
		        }
	        },
	        toVideo: function () {
		        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
			        openFile(this.data.video)
		        }else{
			        openVideo(this.data.video)
		        }
	        },
			openMap: function () {
                openMap(this.data.lng,this.data.lat)
            }
        },
        mounted:function () {
            //falg
            isHomePage(0)
        },
        components:{
        }
    };



