import { mapGetters } from 'vuex'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            isZH:true
        }
    },
    created:function () {
         ///
        if(localStorage.LANGUAGE!='zh'){
            this.isZH = false;
        }
        $(function(){
            $(".navbar-center").css('marginLeft',0);
        });
    },
    methods: {
        goBack:function () {
            this.$router.push('/home');
        },
        robotWash: function() {
            let params = {
                token: localStorage.TOKEN//to: 2206
            };
            //RobotSend
            let dialog = window.YDUI.dialog;
            let alobj = new alertLanguage();
            let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
            let msg = obj.washMsg;
            let sureBnt = obj.sureBnt;
            let title = obj.title;
            this.$store.dispatch('RobotSend', params).then(function (res) {
                msg = obj.successMsg;
                if(res.data.code!=0){
                    msg = obj.sytemBusy;
                }
                dialog.confirm(title,msg, [
                    {
                        txt: sureBnt,
                        color: false,
                        callback: function () {
                            
                        }
                    }
                ]);
            });
        }
    },
    mounted:function () {
        //falg
        isHomePage(0)
    },
    components: {
        
    },
    computed: {
        ...mapState({
            language: state => state.language.language
        })
    },
};



