	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				facData:{},
				info:{},
				pdfShow:false,
				videoShow:false,
				isOne:false,
				isTwo:false,
				pdfFlag:false,
				videoFlag:false,
				btnFlag:false,
                pageFlag:'',
				hotelid:'',
                content:{},
                stateFlag:'',
                showContent:false
			};
		},
        created:function () {
			this.info =  this.$route.query.data
            this.stateFlag = localStorage.stateFlag
            this.$store.dispatch('showLoading')
            var ssrBase = ''
            let _this = this;
			if(this.info.detail){
                $.get(ssrBase+this.info.detail,function (res) {
                    _this.content = res;
                    _this.$store.dispatch('hideLoading')
                    _this.showContent = true
                })
            } else {
                this.$store.dispatch('hideLoading')
            }


			this.pageFlag = localStorage.menuFlag
	        this.facData = this.$route.query.data;
			if(this.facData.pdf && this.info.video){
				this.btnFlag = true;
				this.pdfFlag = true;
				this.videoFlag = true;
				this.isTwo = true;
			}else if(this.facData.pdf){
				this.btnFlag = true;
				this.pdfFlag = true;
				this.isOne = true;
			}else if(this.facData.video){
				this.btnFlag = true;
				this.videoFlag = true;
				this.isOne = true;
			}

			$(function(){
                $(".navbar-center").css('marginLeft',0);
            });

		},
		methods: {
			toPDF:function () {
				if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
					openFile(this.info.pdf)
				}else{
					openPdf(this.info.pdf)
				}

			},
			toVideo: function () {
				if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
					openFile(this.info.video)
				}else{
					openVideo(this.info.video)
				}
			}
		},
        mounted:function () {
            //falg
            isHomePage(0)
        }
	};



