    import { mapState } from 'vuex'
    export default {
        data() {
            return {
                info:{},
                videoUrl:'',
//				sources:[],
                pdfUrl:'',
                videoFlag:false,
	            pdfShow:false,
	            videoShow:false,
	            isOne:false,
	            isTwo:false,
	            pdfFlag:false,
	            videoFlag:false,
	            btnFlag:false,
				content:''
            };
        },
        created:function () {
            this.info = this.$route.query.info;
            this.$store.dispatch('showLoading')
            var ssrBase = ''
            let _this = this;
            if (this.info.detail) {
                $.get(ssrBase+this.info.detail,function (res) {
                    _this.content = res;
                    _this.$store.dispatch('hideLoading')
                })
			} else {
                this.$store.dispatch('hideLoading')
			}

            this.pdfUrl = this.info.pdf;
	        if(this.info.pdf && this.info.video){
		        this.btnFlag = true;
		        this.pdfFlag = true;
		        this.videoFlag = true;
		        this.isTwo = true;
	        }else if(this.info.pdf){
		        this.btnFlag = true;
		        this.pdfFlag = true;
		        this.isOne = true;
	        }else if(this.info.video){
		        this.btnFlag = true;
		        this.videoFlag = true;
		        this.isOne = true;
			}
			
			$(function(){
                $(".navbar-center").css('marginLeft',0);
            });
        },
        methods: {
	        toPDF:function () {
		        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
			        openFile(this.info.pdf)
		        }else{
			        openPdf(this.info.pdf)
		        }

	        },
	        toVideo: function () {
		        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
			        openFile(this.info.video)
		        }else{
			        openVideo(this.info.video)
		        }
	        },
	        closeVideo:function () {
//                this.sources[0].src = '';
		        this.videoFlag = false;
		        this.videoUrl = '';

	        },
	        play() {
	        }

        },
        mounted:function () {
            //falg
            isHomePage(0)
        },
        components:{

        }
    };



