	import { mapGetters } from 'vuex'
    import { mapState } from 'vuex'
	export default {
		data() {
			return {
                dataList: [],
                nextPage: 1,
                noData: false
			};
		},
        created:function () {

            $(function(){
                $(".navbar-center").css('marginLeft',0);
            });
	    },
		methods: {
		    getData:function () {
                let _this = this
                let params = {
                    hotelid: localStorage.HOTELID,
                    lang: localStorage.LANGUAGE,
                    limit:30,
                    page:_this.nextPage
                }
                this.$store.dispatch('getNoticList', params).then(function (res) {
 
                    var arrList = res.data.data.list;
                    _this.dataList = _this.dataList.concat(arrList)
 
                    _this.nextPage = res.data.data.nextPage
                })
            },
            infinite:function (done) {
                let _this = this
                done(true);
                if (this.nextPage != '-1') {
                    _this.getData()
                } else {
                    done(false);
                    _this.noData = true;
                }
            },
            goDetail:function (info) {
                this.$router.push({path:'/opinionDetail', query:{info:info}})
            }
		},
		components: {
		},
        mounted:function () {
            //falg
            isHomePage(0)
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        },

	};



