	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				hotelFlag: true,
				promotionFlag: true,
				surrFlag: true,
				communityFlag: true,
                hotelid: '',
                footShow: true,
                stateFlag: ''  // 
			};
		},
        created:function () {
		    // 
            this.stateFlag = localStorage.stateFlag

		    //tabar
		    if (this.stateFlag == 0) {
		        this.footShow = false
            } else {
                this.footShow = true
            }

			this.hotelid = localStorage.HOTELID;
			switch (localStorage.footFlag){
                case '1':
                	this.hotelFlag =false;
                	this.promotionFlag =true;
	                this.surrFlag =true;
	                this.communityFlag =true;
                	break;
                case '2':
	                this.hotelFlag =true;
	                this.promotionFlag =false;
	                this.surrFlag =true;
	                this.communityFlag =true;
                	break;
                case '3':
	                this.hotelFlag =true;
	                this.promotionFlag =true;
	                this.surrFlag =false;
	                this.communityFlag =true;
                	break;
				case '4':
					this.hotelFlag =true;
					this.promotionFlag =true;
					this.surrFlag =true;
					this.communityFlag =false;
					break;
            }

        },
		methods: {
			/**
			 * 
             *  1.stateFlag = 0  2.stateFlag = 1  3.stateFlag = 2 
			*/
			gohome:function () {
			    if (this.stateFlag == 1) {
                    this.$router.replace('/home')
                } else {
                    this.$router.replace('/s_home')
                }
			},
            //promotion 
            goPromotion:function () {
                if(localStorage.menuFlag != 'unlogin'){
                    this.$router.replace('/promotion')
                }else {
	                this.$router.replace({path:'/loginforguest', query:{pageType:this.hotelid}})
                }
            },
			//surroundings 
			goSurround:function () {
				if(localStorage.menuFlag != 'unlogin'){
					this.$router.replace('/raiders')
				}else {
					this.$router.replace({path:'/loginforguest', query:{pageType:this.hotelid}})
				}
			},
			//communtiy 
			goCommuntiy:function () {
				if( localStorage.TOKEN ){
					this.$router.replace('/communityLife')
				}else {
					this.$router.replace({path:'/loginforguest', query:{pageType:this.hotelid}})
				}
			}
		},
		computed: {
			...mapState({
				language: state => state.language.language
			})
		},
	};



