
import AlloyFinger from 'alloyfinger'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { mapGetters } from 'vuex'
import menu from '../common/menu.vue'
export default {
data() {
return {
menuFlag:false,
menus:{
en:'English',
zh:''
},
markFlag:false,
menuShow:false
};
},
created:function () {

localStorage.visit = 1;

localStorage.menuFlag = 'guide';

this.$store.dispatch('firstLanguage');

$(function(){
$(".navbar-center").css('marginLeft',0);
});
},
mounted(){
let _this = this;

var alloyTouch = new AlloyTouch({
touch:".guide_bottom",//dom
vertical: true,//truetouch

property: "translateY",  //
min: 100, //,
max: 2000, //,
sensitivity: 1,//,1
factor: 1,//,1
step: 45,//step
bindSelf: false,
initialValue: 0,
change:function(value){  },
touchStart:function(evt, value){  },
touchMove:function(evt, value){  },
touchEnd:function(evt,value){
_this.$router.push('/unlogin')

},
tap:function(evt, value){  },
pressMove:function(evt, value){  },
animationEnd:function(value){  } //
})


},
methods: {

showSlide:function () {
this.menuShow = true;
if(this.menuFlag == false){
$('.guide').animate({
'left':'-5rem',
},500)
$('.mark').animate({
'left':'-5rem',
},500);
this.menuFlag = true;
this.markFlag = true;
}
},

goSearch:function () {

this.$router.push({path:'/search',query:{searchFlag:'guide'}});
},

hideSlide:function () {
if(this.menuFlag == true){
$('.guide').animate({
'left':0,
},500)
$('.mark').animate({
'left':0,
},500);
this.menuFlag = false;
this.markFlag = false;
}
},

goHome:function () {


},

goLogin:function () {
this.$router.push('/loginforguest')
},
hideAction:function (key) {

let params = {
token: localStorage.TOKEN,
platform: localStorage.platform,
identity:localStorage.identity,
lang:key

}
this.$store.dispatch('tabLanguage', params).then((res) => {
if(res.code == 0){

localStorage.LANGUAGE = key;
this.$store.dispatch('updateLanguage',key)

}else{
this.$dialog.toast({mes: res.msg, timeout: 1000});
}
})

},



},
components: {
'v-menu': menu,
Actionsheet
},
computed: {
...mapGetters([
'language',
'tabLanShow'
])
},
mounted:function () {

isHomePage(0)
}
};



