methods: {

showMenu: function() {
this.menuFlag = true; //
this.centerFlag = false; //
$(".index").css({
position: "absolute",
height: "100%",
width: "100%",
overflow: "hidden",
"z-index": "350"
});
if (this.menuSlide == false) {
$(".index").css({
left: "-5rem"
});
$("footer").css({
left: "-5rem"
});

$(".mark").css({

opacity: 1
});
this.menuSlide = true;
this.markFlag = true;
}
},

showCenter: function() {
$(".index").css({
position: "absolute",
height: "100%",
width: "100%",
overflow: "hidden",
"z-index": "350"
});
this.menuFlag = false; //
this.centerFlag = true; //
if (this.centerSlide == false) {
$(".index").css({
left: "4.5rem"
});
$("footer").css({
left: "4.5rem"
});
$(".mark").css({
opacity: 1
});
this.centerSlide = true;
this.markFlag = true;
}
},

hideSlide: function() {
let _this = this;

if (this.menuSlide) {
$(".index").css({
left: 0
});
$("footer").css({
left: 0
});
$(".mark").css({
opacity: 0
});
this.menuSlide = false;
this.markFlag = false;
setTimeout(function() {
_this.menuFlag = false;
}, 200);
}

if (this.centerSlide) {
$(".index").css({
left: 0
});
$("footer").css({
left: 0
});
$(".mark").css({
opacity: 0
});
this.centerSlide = false;
this.markFlag = false;
setTimeout(function() {
_this.centerFlag = false;
}, 300);
}
},
hideAction: function(key) {
let _this = this;

localStorage.LANGUAGE = key;
let params1 = {
token: localStorage.TOKEN,
platform: localStorage.platform,
identity: localStorage.identity,
lang: key
};
this.$store.dispatch("tabLanguage", params1);
this.$store.dispatch("updateLanguage", key);
this.$store.dispatch("changeLanguage");

let params = {
hotelid: this.hotelid,
lang: localStorage.LANGUAGE
};
this.$store.dispatch("getHome", params).then(function(res) {
console.log(res);
_this.shortcutList = [];

for (var item in _this.home.data.shortcutList) {
for (var i in _this.localshortcutList) {
if (
_this.home.data.shortcutList[item].key ==
_this.localshortcutList[i].key
) {
_this.localshortcutList[i].title =
_this.home.data.shortcutList[item].title;
_this.shortcutList.push(_this.localshortcutList[i]);
}
}
}
});


let params2 = {
hotelid: this.hotelid,
lang: localStorage.LANGUAGE
};
this.$store.dispatch("getHotelDetail", params2);


if(key!='zh'){
this.servicesMsg = 'Services';
}else{
this.servicesMsg = '';
}
},

goLocation: function(val) {






































this.$router.push("/map");

},
goNext: function(key) {
let _this = this;
switch (key) {
case "control":
_this.$router.push("#");
break;
case "reservation":
if (localStorage.TOKEN) {
openBooking(localStorage.bookUrl);
} else {
_this.$router.replace("/loginforguest");
}
break;
case "tanslate":
_this.$router.push("/translate");
break;
case "service":
if (localStorage.TOKEN) {
if (localStorage.idType == 1) {
if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
openFile(localStorage.serviceUrl);
} else {
openUrl(localStorage.serviceUrl, " ");
}
} else {
alert("");
}
} else {
_this.$router.replace("/loginforguest");
}
break;
case "flight":
_this.$router.push("#");
break;
case "phone":
_this.$router.push("/phone");
break;
case "bills":
_this.$router.push("/mybill");
break;
case "invoice":
if (localStorage.TOKEN) {
_this.$router.push("/invoices");
} else {
_this.$router.replace("/loginforguest");
}
break;
case "news":
_this.$router.push("/ssr");
break;
case "lifeservice":
_this.$router.push("/lifeStyle");
break;
case "shop":
if (localStorage.TOKEN) {

if(localStorage.idType==1){
checkPin(_this,{
path: "/shopping",
query: { pageFlag: "home" }
});
}else{
_this.$router.push({
path: "/shopping",
query: { pageFlag: "home" }
});
}
} else {
_this.$router.replace("/loginforguest");
}

break;
case "breakfast":
if (localStorage.TOKEN) {
checkPin(_this,{
path: "/breakfast",
query: { pageFlag: "home" }
});
} else {
_this.$router.replace("/loginforguest");
}
break;
case "supermarket":
if (localStorage.TOKEN) {
_this.$router.push({
path: "/lifeSohp",
query: { pageFlag: "home" }
});
} else {
_this.$router.replace("/loginforguest");
}
break;
case "laundry":
_this.$router.push({
path: "/laundry",
query: { pageFlag: "home" }
});
break;
}
},
goBack: function() {
if (this.$route.query.page == "unlogin" || localStorage.spage == 1) {
this.$router.push("/unlogin");
} else if (
this.$route.query.page == "search" ||
localStorage.spage == 2
) {
this.$router.push("/search");
}
},
goTraffic: function(item) {
this.$router.push({ path: "/home/trafficDetail", query: { info: item } });
},
openBooking: function(url) {
openBooking(url);
}
},
components: {
"v-foot": foot,
"v-menu": menu,
"v-guestCenter": guestCenter,
Actionsheet
},
computed: {
...mapState({
language: state => state.language.language,
home: state => state.home.home,
tabLanShow: state => state.language.tabLanShow,
hotelDetail: state => state.home.hotelDetail
})
}
};



