    import { mapGetters } from 'vuex'
    import { mapState } from 'vuex'
    import foot from '../foot.vue'
	import menu from '../common/menu.vue'
    import guestCenter from '../common/menu/guestCenter.vue'
	export default {
		data() {
			return {
                menuFlag:false,
                centerFlag:false
			};
		},
        created:function () {
			//
			localStorage.footFlag = 4;
        },
        mounted:function () {
            //falg
            isHomePage(1)
        },
        methods: {
            goShopping: function() {
                let _this = this;
                //pin
                if(localStorage.idType==1){
                    checkPin(this,{path:'/shopping', query:{info:'communityLife'}});
                }else{
                    _this.$router.push({path:'/shopping', query:{info:'communityLife'}});
                }
            }
        },
        components:{
			'v-foot':foot,
            'v-menu':menu,
            'v-guestCenter':guestCenter
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        },
	};



