	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				panoramicSrc:'',
				tabIndex:'',
                detail:'',
                pdf:'',
                video:'',
                content:'',
                showContent:false,
                title:this.$route.query.title,
                showPdf: false,
                showVideo:false
			};
		},
		created:function () {
	        this.tabIndex = this.$route.query.index;
	        this.detail = this.$route.query.detail;
	        this.pdf = this.$route.query.pdf;
            this.video = this.$route.query.video;
            this.showPdf = this.pdf==''?false:true;
            this.showVideo = this.video==''?false:true;
            this.$store.dispatch('showLoading')
	        this.panoramicSrc = this.detail;
            var ssrBase = ''
            let _this = this;
            if(this.panoramicSrc){
                $.get(ssrBase+this.panoramicSrc,function (res) {
                    _this.content = res;
                    _this.$store.dispatch('hideLoading')
                    _this.showContent = true
                })
            } else {
                this.$store.dispatch('hideLoading')
            }

            $(function(){
                $(".navbar-center").css('marginLeft',0);
            });
        },
        methods: {
            toPDF:function () {
if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
openFile(this.pdf);
}else{
openPdf(this.pdf);
}

            },
            toVideo: function () {
if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
openFile(this.video);
}else{
openVideo(this.video);
}
}
		},
        mounted:function () {
            //falg
            isHomePage(0)
        }
	};



