	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
			    orderList:[],
                nextPage:1,
                noData: false
			}
		},
        created:function () {

            $(function(){
                $(".navbar-center").css('marginLeft',0);
            });
        },
		methods: {
            getData:function(){
                let _this = this
                let params = {
                    token: localStorage.TOKEN,
                    hotelid: localStorage.HOTELID,
                    limit: 10,
                    page:_this.nextPage
                }
                this.$store.dispatch('getOrderList', params).then(function (res) {
                    var arrList = res.data.list
                    _this.orderList = _this.orderList.concat(arrList)
                    _this.nextPage = res.data.nextPage
                })
            },
            infinite:function (done) {
                let _this = this
                done(true);
                if (this.nextPage != '-1') {
                    _this.getData()
                } else {
                    done(false);
                    _this.noData = true;
                }
            },
            changeOrder:function (status, id) {
                if(status<3){
                    status = Number(status)+1
                }
                let _this = this
                let params = {
                    token: localStorage.TOKEN,
                    orderid:id,
                    status:status
                }
                this.$store.dispatch('changeOrder', params).then(function (res) {
                    if(res.code == 0){
                        _this.$dialog.toast({mes: '', timeout: 1000});
                    } else {
                        _this.$dialog.toast({mes: res.data.msg, timeout: 1000});
                    }

                })
            }
		},
        mounted:function () {
            //falg
            isHomePage(0)
        },
        components:{
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        }
	};



