
import { mapGetters } from 'vuex'
import foot from '../foot.vue'
import menu from '../common/menu.vue'
import guestCenter from '../common/menu/guestCenter.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { mapState } from 'vuex'
export default {
data() {
return {
dataList:[],
tagList:[],
nextPage:'1',
noData:false,
menuFlag:1,
isItemShow:false
}
},
created:function () {

localStorage.footFlag = 2

let _this = this;
$(document).ready(function () {
var swiper = new Swiper('.promotiom .swiper-container', {
pagination: '.swiper-pagination',
slidesPerView: 2.8,
paginationClickable: true,
spaceBetween: 0
});

$('.promotiom_index .swiper-wrapper>div').click(function () {
$(this).addClass('active').siblings().removeClass('active');
})

})
},
mounted:function () {

isHomePage(1)
},
methods: {
infinite(done){
let _this = this
done(true);

if(this.nextPage != '-1'){
if(this.menuFlag == 1){
this.getPromotionList('inifinte')
}else if(this.menuFlag == 2){
this.getNewsList('inifinte')
}else if(this.menuFlag == 3){
this.getGroupNewsList('inifinte')
}else if(this.menuFlag == 4){
this.getGroupNoticeList('inifinte')
}

}else{
done(false);
this.noData = true;
}
},
getPromotionList:function (flag) {
this.menuFlag = 1
let _this =this
if(flag != 'inifinte'){
this.dataList=[]
}
setTimeout(function () {
let params = {
hotelid: localStorage.HOTELID,
lang: localStorage.LANGUAGE,
tagid:'',
page:_this.nextPage,
limit:30
}
_this.$store.dispatch('getPromotionList', params).then((res) => {
_this.dataList = _this.dataList.concat(res.data.data.list)
_this.nextPage = res.data.data.nextPage
_this.getFlag(_this.dataList)
})
},300)
},
getNewsList:function (flag) {
this.menuFlag = 2
let _this =this
if(flag){
this.dataList=[]
}
setTimeout(function () {
let params = {
hotelid: localStorage.HOTELID,
lang: localStorage.LANGUAGE,
tagid:'',
page:_this.nextPage,
limit:30
};
_this.$store.dispatch('getNewsList', params).then((res) => {
_this.dataList = _this.dataList.concat(res.data.data.list)
_this.nextPage = res.data.data.nextPage
_this.getFlag(_this.dataList)
})
},300)

},
getGroupNewsList:function (flag) {
this.menuFlag = 3
let _this =this
if(flag){
this.dataList=[]
}
setTimeout(function () {
let params = {
groupid: localStorage.groupid,
tagid:'',
page:_this.nextPage,
limit:30
};
_this.$store.dispatch('getGroupNewsList', params).then((res) => {
_this.dataList = _this.dataList.concat(res.data.data.list)
_this.nextPage = res.data.data.nextPage
_this.getFlag(_this.dataList)
})
},300)
},
getGroupNoticeList:function (flag) {
this.menuFlag = 4
let _this =this
if(flag){
this.dataList=[]
}
setTimeout(function () {
let params = {
groupid: localStorage.groupid,
tagid:'',
page:_this.nextPage,
limit:30
};
_this.$store.dispatch('getGroupNoticeList', params).then((res) => {
_this.dataList = _this.dataList.concat(res.data.data.list)
_this.nextPage = res.data.data.nextPage
_this.getFlag(_this.dataList)
})
},300)
},
changeMenuFlag(flag) {

this.dataList=[];
this.nextPage = '1';
this.menuFlag = flag;
},
goDetail(id) {
let data = {};
for(var key in this.dataList){
if (this.dataList[key].id == id){
data = this.dataList[key];
break;
}
}
this.$router.push({path:'/promotionDetail',query:{info:data}});
},
getFlag:function (dataList) {
this.tagList= [];

for(var item in dataList){
if(this.dataList[item].tagName !=''){
this.tagList.push(this.dataList[item].tagName)
}
}
}
},
components:{
'v-foot':foot,
'v-menu':menu,
'v-guestCenter':guestCenter,
Divider,
Spinner,
PopupPicker,
},
computed: {
...mapState({
language: state => state.language.language
})
},
};



